import {Component, ViewChild} from '@angular/core';
import {Tabs} from 'ionic-angular';
import { SettingsPage } from '../settings/settings';
import { ExamsPage } from '../exams/exams';

class TabModel {
    constructor(
      public title: string,
      public page: any,
      public icon: string = null,
      public badgeCount: number = 0,
      public badgeStyle: string = null
    ){
    }
  }

@Component({
    selector:'students',
    templateUrl: './students.html'
})
export class StudentsPage{
    @ViewChild(Tabs)
    public tabsComponent : Tabs;
    public tabs : TabModel[] = [];
    constructor(){
        this.tabs = [
          new TabModel('Exams', ExamsPage, 'albums'),
          new TabModel('Settings', SettingsPage, 'settings')
          ];
    }
    ionViewDidEnter(){
        this.tabsComponent.select(2);
    }
}


